import React, { useEffect, useState } from 'react';
import './style.scss';
import $ from 'jquery';
import 'jquery.easing';
import image from '../../../assets/img/pg/sup.jpg';
import wave1 from '../../../assets/img/wl/wave1.png';
import wave2 from '../../../assets/img/wl/wave2.png';
import foguete from '../../../assets/img/wl/foguete.png';

const Solucoes = () => {

    
    const active = (e) => {
        if($("#solucoes-pg").offset().top - $(window).scrollTop() < (window.screen.height) && !$(".solucoes-pg").hasClass("animate")) {
            $(".solucoes-pg").addClass("animate");
        }
    }
    
    useEffect(() => {
        $(window).on('scroll',()=>active());
    },[]);

    return  (
        <section className="solucoes-pg" id='solucoes-pg'>
            {/* <div className='container'>
                <h2 className='default-title'>MDIA DE GANHOS DAS AGNCIAS PARCEIRAS<span className='default-subtitle'>*Essa  uma mdia de MRR, valores a serem
definidos no momento do contrato.</span></h2>
            </div> */}
            <div className='container'>
                <div className="solucoes-pg__left">
                    <div className='solucoes-pg__left--item'>
                        <h3>Design Personalizado:</h3>
                        <p>Criamos sites que refletem a identidade nica de cada cliente. Seu site ser uma vitrine virtual que no apenas atende s suas necessidades comerciais, mas tambm impressiona visualmente."</p>
                    </div>
                    <div className='solucoes-pg__left--item'>
                        <h3>Processo de Desenvolvimento Rpido e Eficiente:</h3>
                        <p>Nosso servio simplifica o processo de criao de sites, desde a concepo at a implementao. Trabalhamos de forma eficiente para garantir que seu site esteja online o mais rpido possvel, sem comprometer a qualidade."</p>
                    </div>
                    <div className='solucoes-pg__left--item'>
                        <h3>Suporte Contnuo e Atualizaes:</h3>
                        <p>Alm da criao inicial, oferecemos suporte contnuo e atualizaes para garantir que seu site esteja sempre atualizado com as ltimas tendncias e tecnologias, mantendo-o competitivo online"</p>
                    </div>
                </div>
                <div className="solucoes-pg__right">
                    <img  src={image} alt=''/>
                </div>
            </div>
        </section>
    )
}

export default Solucoes;