import React, { useEffect } from 'react';
import './style.scss';
import image from '../../assets/img/metodo.svg';
import banner1 from '../../assets/img/banner.jpg';
import logo from '../../assets/img/logo.png';
import $ from 'jquery';


const About = () => {

    const active = (e) => {
        if($("#about").offset().top - $(window).scrollTop() < (window.screen.height) && !$(".about").hasClass("animate")) {
            $(".about").addClass("animate");
        }
    }
    
    useEffect(() => {
        $(window).on('scroll',()=>active());
    },[]);

    return  (
        <section className="about" id='about' style={{background: `url(${banner1})`}}>
             <div className='container'>
                <h2 className='default-title'>Quem Somos</h2>
                <img className='' src={logo} alt=''/>
            </div>
            <div className="container">
                <p><b>Escritrio digital</b> focado em estratgias de escala para negcios que j atuam na
                internet.</p>
                <p>Nossa dinmica de servio nos permite ter os <b>melhores profissionais</b> para o seu
                projeto  nossa disposio.</p>
                <p>Contamos com parceiros de negcios no <b>Brasil, Estados Unidos, Argentina e
                Portugal.</b></p>
                <p>Atualmente nossa capacidade  de <b>20 clientes</b> simultneos e temos sob gesto 
                <b> R$1 milho</b> / ano de oramento em mdia.</p>
            </div>
        </section>
    )
}

export default About;